import BaseApiService from "services/BaseApiService";
const DEFAULT_PATH = "/custom/api";

class OtherDocumentService extends BaseApiService {
  getDocsByApplicantType = (stageName: string, loanUid: string, applicantUid: string) => {
    const url = `${DEFAULT_PATH}/${stageName}/${loanUid}/${applicantUid}/otherdocument`;
    return this.makeGetRequestWithAuth(url);
  };

  getDocument = (loanUid: string) => {
    const url = `${DEFAULT_PATH}/login/${loanUid}/otherdocument`;
    return this.makeGetRequestWithAuth(url);
  };

  saveDocument = (stageName: string, subStageName: string, payload: any) => {
    const url = `${DEFAULT_PATH}/${stageName}/${
      subStageName ? subStageName + "/" : ""
    }otherdocument`;
    return this.makePutRequestWithAuth(url, payload);
  };

  getDocsByStage = (stageName: string, stageUid: string) => {
    const url = `${DEFAULT_PATH}/${stageName}/otherdocument/${stageUid}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new OtherDocumentService();
